import React from 'react'
import './index.css'
import {Card, CardDeck} from 'react-bootstrap'

function ListImages(props)
{   
    const images = props.images
    const imageList = Object.keys(images).map((key) => 
        <Card style={{ width: '18rem' }} key={key}>
        <Card.Header ><h5>{key}</h5></Card.Header>
        <div className="topContainer"> 
        {images[key].map((picture)=>(<div key={picture}>
        <Card.Img variant="top" src={"https://tylergoh.tk/static/" + props.currentUser + '/face/images/' + key + "/" + picture} />
        </div>))}
        </div>
        </Card>
      )
    return(
        <div classname="listImages" style={{float:"left"}}>
        <CardDeck>
        {imageList}
        </CardDeck>
        </div>
    );
}

export default ListImages;