import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Button, Input, Form,Icon } from 'antd';
import { Redirect } from 'react-router-dom'
import axios from 'axios'
import { isTerminatorless, isFunctionTypeAnnotation } from '@babel/types';

function Login(props) {
  const [loggingIn, setLoggingIn] = useState(true)
  const [username,setUsername] = useState("")
  const [password,setPassword] = useState("")
  const [usernameStatus, setUsernameStatus] = useState("")
  const [usernameHelp, setUsernameHelp] = useState("")
  const [passwordStatus, setPasswordStatus] = useState("")
  const [passwordHelp, setPasswordHelp] = useState("")

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };
  if (props.userSignedIn === true)
      return(<Redirect to='/' />)

  return (
      
      <div style={{ backgroundColor: '#f0f2f5', height: '100%', paddingTop: 130 }}>
        <div>
          <Card style={{ width: 500, marginLeft: 'auto', marginRight: 'auto', marginBottom: 130, borderRadius: 10, textAlign: 'center' }}>
            <Row>
              <h1 style={{ margin: "15px 0" }}>ILKR</h1>
            </Row>
            <Row>
              <Col span={8} offset={4}>
                <Button onClick={() => {setLoggingIn(true); clearAll()}} type={loggingIn ? "primary" : "default"} 
                        style={{ width: '100%', borderBottomRightRadius: 0, borderTopRightRadius: 0 }}>Log In</Button>
              </Col>
              <Col span={8}>
                <Button onClick={() => {setLoggingIn(false); clearAll()}} type={loggingIn ? "default" : "primary"} 
                        style={{ width: '100%', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }}>Sign Up</Button>
              </Col>
            <Row style={{ margin: "25px 0" }}></Row>  
            </Row>
            <Form {...formItemLayout}>
            <Form.Item style={{marginLeft:"25%"}}hasFeedback validateStatus={usernameStatus} help={usernameHelp}>
            <Input name="username" prefix={<Icon type="user"/>} autoComplete = "off" style={{ width: '132.5%' }} 
                   size="large" onChange={(e) => usernameChange(e)} onPressEnter={() => register()} placeholder="Username"/>
            </Form.Item>
            <Form.Item style={{marginLeft:"25%"}} hasFeedback validateStatus={passwordStatus} help={passwordHelp}>
            <Input name="password" prefix={<Icon type="lock"/>} style={{width: '132.5%' }} type="password" size="large" 
                   onChange={(e) => passwordChange(e)} onPressEnter={() => register()} placeholder="Password"/>
            </Form.Item>
            <Row style={{ margin: "25px 0" }}></Row>
              <Button onClick={() => register()} size="large" type="primary">Enter</Button>
            </Form>
          </Card>
          <Row></Row> 
        </div>
      </div>
    )
  function clearAll(){
    setUsernameStatus('')
    setPasswordStatus('')
    setUsernameHelp('')
    setPasswordHelp('')
  }

  function usernameChange(e){
  setUsername(e.target.value)
  if(!loggingIn)
  {
    if(e.target.value.length<8) 
    {
      setUsernameStatus('error')
      setUsernameHelp("Username must be at least 8 characters")
    }
    else
    {
      setUsernameStatus("")
      setUsernameHelp("")        
    }
  }
}

  function passwordChange(e){
    setPassword(e.target.value)
    if(e.target.value.length<8 && !loggingIn) 
    {
      setPasswordStatus('error')
      setPasswordHelp("Password must be at least 8 characters")
    }
    else
    {
      setPasswordStatus("")
      setPasswordHelp("")   
    }
  }
  function register() {
    let registration = "login"
    if (!loggingIn)
      registration = "register"
      axios.post(`https://tylergoh.tk/${registration}`, {
        name: username,
        password: password
    })
    .then(res => {
      console.log(res.data)
      if(res.data === "success")
      {
      props.setUserSignedIn(true);
      props.setCurrentUser(username);
      localStorage.setItem('username', username)
      localStorage.setItem('password', password);
      }
      if(res.data === "failure")
      {
      setUsernameStatus("error")
      setUsernameHelp("Username is taken")
      }
      if(res.data === "wrongPass")
      {
        setPasswordStatus("error")
        setPasswordHelp("Invalid password")  
        setUsernameStatus('')
        setUsernameHelp('')
      }
      if(res.data === "wrongUser")
      {
        setUsernameStatus("error")
        setUsernameHelp("Username does not exist")   
        setPasswordStatus('')
        setPasswordHelp('')
      }
    })
    .catch(err => console.log(err))
  }
}
    

export default Login