import React, {useEffect, useState} from 'react';
import {useDropzone} from 'react-dropzone';
import {Button , message, Form , Input , Row} from 'antd'
import axios from 'axios'
import {Redirect} from 'react-router-dom'
import './index.css'
import ListImages from './listImages'

const thumbsContainer = {
  display: 'flex',
  flexDirection: 'row',
  flexWrap: 'wrap',
  marginTop: 16
};

const thumb = {
  display: 'inline-flex',
  borderRadius: 2,
  border: '1px solid #eaeaea',
  marginBottom: 8,
  marginRight: 8,
  width: 100,
  height: 100,
  padding: 4,
  boxSizing: 'border-box'
};

const thumbInner = {
  display: 'flex',
  minWidth: 0,
  overflow: 'hidden'
};

const img = {
  display: 'block',
  width: 'auto',
  height: '100%'
};


function Face(props) {
  const [files, setFiles] = useState([]);
  const [images, setImages] = useState({});
  const [name,setName] = useState("")
  const [nameStatus, setNameStatus] = useState("")
  const [nameHelp, setNameHelp] = useState("")
  const [disabledTrain, setDisabledTrain] = useState(true)
  const [disabledClear, setDisabledClear] = useState(true)
  const [disabledUpload, setDisabledUpload] = useState(false)
  const [redirect, setRedirect] = useState(false)
  const [loading, setLoading] = useState(false)
  const {getRootProps, getInputProps} = useDropzone({
    accept: 'image/*',
    onDrop: acceptedFiles => {
      setFiles(acceptedFiles.map(file => Object.assign(file, {
        preview: URL.createObjectURL(file)
      })));
      console.log(files)
    }
  });
  
  const thumbs = files.map(file => (
    <div style={thumb} key={file.name}>
      <div style={thumbInner}>
        <img
          src={file.preview}
          style={img}
        />
      </div>
    </div>
  ));

  function getImages(){
    if(props.userSignedIn === true)
    {
    var str = props.currentUser.replace(/\s+/g, '%20');
    axios.post('https://tylergoh.tk:4000/faceGetImages/' + str).then(response=>{
      setImages(response.data)
      if(Object.keys(response.data).length>1)
      setDisabledTrain(false)
      setDisabledClear(false)
    })
  }
  }

  const Clear = () => {
    var str = props.currentUser.replace(/\s+/g, '%20');
    axios.post('https://tylergoh.tk:4000/faceClearImages/' + str).then(response=>{
      message.success("Images cleared")
      setImages({})
      setDisabledClear(true)
      setDisabledTrain(true)
    })
    .catch(()=>{message.error('Unable to clear')})
  
}
  const Train = () =>{
    var str = props.currentUser.replace(/\s+/g, '%20');
    setDisabledClear(true)
    setLoading(true)
    setDisabledUpload(true)
    axios.post('https://tylergoh.tk:4000/faceTrain/' + str).then(response=>{
      setRedirect(true)
    })
    .catch(()=>{
      setLoading(false)
      setDisabledClear(false)
      setDisabledUpload(false)
      message.error('Unable to train')})
  }

  const Submit = () => {
    if(name == "")
    {
      setNameHelp("Name required")
      setNameStatus("error")
      return
    }
    if(files.length>0)
    {
    const form = new FormData();
    form.append('user', props.currentUser)
    form.append('name', name)             
    for(var i in files){
    form.append('file', files[i])
    }
    axios.post('https://tylergoh.tk:4000/faceUpload', form).then(response => {
      setFiles([])
      message.success('Sucessful upload')
      getImages();
      
    }).catch(err => {
      console.log(err);
      message.error('Upload failed')
    });     
    }
}

  useEffect(()=>{
    if (props.userSignedIn === true)
    getImages()
  },[]);

  useEffect(() => () => {
 
    // Make sure to revoke the data uris to avoid memory leaks
    files.forEach(file => URL.revokeObjectURL(file.preview));
  }, [files]);

  
  if (props.userSignedIn === false)
  return(<Redirect to='/login' />)
  if (redirect === true)
  return(<Redirect to='/train' />)
  return (
  <div style={{padding : "10px"}}>
      <Row>
      <Form>
      <Form.Item hasFeedback validateStatus={nameStatus} help={nameHelp}>
      <Input name="name" autoComplete = "off" style={{ width: '100%' }} size="large" onChange={(e) => {setName(e.target.value);    setNameHelp(""); setNameStatus("");}} placeholder="Enter the name of the person"/>
      </Form.Item>
      </Form>
      <div {...getRootProps({className: 'dropzone'})}>
        <input {...getInputProps()} />
        <h3>Upload files of the person</h3>
      </div>
      <aside style={thumbsContainer}>
        {thumbs}
      </aside>
       <Button type="primary" htmlType="submit" onClick ={Submit} disabled={disabledUpload}>
      Upload
      </Button>
    </Row>
    <Button type="primary" loading={loading} htmlType="submit" onClick ={Train} style={{marginRight:"30px",marginTop:"30px",marginBottom:"30px"}} disabled={disabledTrain}>
      Train
    </Button>
      <Button type="primary" htmlType="submit" onClick ={Clear} disabled={disabledClear}>
      Clear
      </Button>
    <Row>
    <ListImages images = {images} currentUser = {props.currentUser}></ListImages>
    </Row>
    </div>
   
  );
}


export default Face;



