import React,{useState} from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import { QueryParamProvider } from 'use-query-params'
import './App.css';
import Main from './components/main';
import About from './components/about';
import Navigation from './components/navigation'
import Login from './components/login'
import Face from './components/face'
import FaceTest from './components/faceTest'
import Audio from './components/Audio'

function App() {

  const [userSignedIn, setUserSignedIn] = useState(false);
  const [currentUser, setCurrentUser] = useState();
  const [isPlaying, setIsPlaying] = useState(false);
  const [hideAudio, setHideAudio] = useState(true);

  if(userSignedIn === false)
  {
    if(localStorage.getItem('username'))
    {
      setUserSignedIn(true)
      setCurrentUser(localStorage.getItem('username'))
    }
  }
  return (
    <div className="App">
      <div className={hideAudio ? 'hidden' : ''}>
        <Audio setIsPlaying = {setIsPlaying}/>
      </div>
      <Router>
        <QueryParamProvider>
        <Navigation 
          currentUser = {currentUser} 
          userSignedIn = {userSignedIn}
          setUserSignedIn = {setUserSignedIn}
          setCurrentUser = {setCurrentUser}/>
        <Switch>
          <Route path="/" exact render={(props) =>
            <Main {...props}
            currentUser = {currentUser}
            userSignedIn = {userSignedIn} />} />
          <Route path="/about" render={(props)=>
          <About {...props}
            isPlaying = {isPlaying}
            setHideAudio = {setHideAudio}/>} />
          <Route path="/face" render={(props) =>
          <Face {...props}
            currentUser = {currentUser}
            userSignedIn = {userSignedIn} />} />
          <Route path="/train" render={(props) =>
          <FaceTest {...props}
          currentUser = {currentUser}
          userSignedIn = {userSignedIn} />} />
          <Route path="/login" render={(props) => 
          <Login {...props} 
            setCurrentUser = {setCurrentUser} 
            setUserSignedIn = {setUserSignedIn} 
            userSignedIn = {userSignedIn}/>} />
        </Switch>
        </QueryParamProvider>
      </Router>
    </div>
  );
}

export default App;
