import React, { useState, useEffect, useRef } from 'react';
import {Button, message} from 'antd'
import axios from 'axios'

function FaceTest(props) {
    const inputs = useRef(null);
    const [visible, setVisible] = useState(false)
    const [loading, setLoading] = useState(false)
    const [key,setKey] = useState("")
    return(
        <div>
        <img src={"https://tylergoh.tk/static/" + props.currentUser + '/face/test/result.png'}/> 
        <input ref={inputs} type='file' id='single' accept="image/png, image/jpeg" onChange={(e)=>onChange(e)} style={{display:"none"}} /> 
        <br/>
        <Button loading={loading} type="primary" htmlType="submit" onClick={()=>{inputs.current.click()}}>
        Upload
        </Button>
        <br/>
        {visible &&
            <div style={{padding:"20px"}}>
            <a href={"https://tylergoh.tk/static/" + props.currentUser + '/face/test/result/' + key + '.jpg'} target="_blank">
            <img src={"https://tylergoh.tk/static/" + props.currentUser + '/face/test/result/' + key + '.jpg'} width={640} /> 
            </a>
            </div>
        }
        </div>
    );

    function onChange(e){
        setLoading(true)
        setVisible(false)
        const form = new FormData();
        form.append('user', props.currentUser)    
        form.append('file', e.target.files[0])
        axios.post('https://tylergoh.tk:4000/faceTest', form).then(response => {
        setKey(response.data)
        setVisible(true)
        setLoading(false)
        }).catch(err => {
        message.error('Upload failed')
        setLoading(false)
    })}
}

export default FaceTest