import React from 'react';
import { BrowserRouter as Router, Link ,Route, Switch } from "react-router-dom";
import { Menu } from 'antd';
import SubMenu from 'antd/lib/menu/SubMenu';


function Navigation(props) {
    return (
        <div className='navigation'>
        <Menu
            mode="horizontal"
            style={{ padding: '7px' , width:'100%' , textAlign: 'right', fontSize:'15px'}}
            theme="dark"
        >
            <SubMenu title="AI">
                <Menu.Item key="face" style={{fontSize:'15px'}}>
                    <Link to='/face'>
                        Face Recognition
                    </Link>
                </Menu.Item>
            </SubMenu>
            <Menu.Item key="main">
                <Link to='/' >
                    App
                </Link>
            </Menu.Item>
            <Menu.Item key="about">
                <Link to='/About'>
                    Music
                </Link>
            </Menu.Item>
            { props.userSignedIn? 
                (   <SubMenu title={<span>{props.currentUser}</span> }>
                        <Menu.Item key="login" style={{fontSize:'15px'}} onClick={() => {props.setCurrentUser(null); props.setUserSignedIn(false);localStorage.removeItem("username"); localStorage.removeItem("password");}}>
                            <Link to='/login'>
                                Logout
                            </Link>
                        </Menu.Item>
                    </SubMenu>)
 
                :(  <Menu.Item key="login" style={{fontSize:'15px'}}>
                        <Link to='/Login'>
                        Login
                    </Link>
                    </Menu.Item>)
            }
           
        </Menu>
        </div>
    )
}

export default Navigation